}

body {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-family: "Ubuntu", sans-serif;
  -ms-overflow-style: scrollbar;
  -webkit-font-smoothing: subpixel-antialiased;
  color: #28282A;
}

button {
  box-shadow: none !important;
}

ol,
ul {
  list-style: none;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.primary-gradient {
  background: linear-gradient(91.02deg, #0b0c0e 0%, #072448 100%);
}

h1,
.h1 {
  font-size: 43px;
  line-height: 45px;
}

h2,
.h2 {
  font-size: 38px;
  line-height: 45px;
}

h3,
.h3 {
  font-size: 31px;
  line-height: 41px;
}

h4,
.h4 {
  font-size: 27px;
  line-height: 35px;
}

h5,
.h5 {
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
}

h6,
.h6 {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
}

.text-generator {
  background: linear-gradient(91.02deg, #0b0c0e 0%, #072448 85.09%, #0b0c0e 102.7%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.badge {
  color: #fff;
  font-weight: 700;
  font-size: 10px;
  line-height: 18px;
  letter-spacing: 0.02em;
  background-color: #94DB33;
  border-radius: 0px;
  padding: 0px 14px;
}

.btn-light span {
  color: #101112 !important;
  position: relative;
  z-index: 2;
}
.btn-light::before {
  background: #101112 !important;
}
.btn-light:hover span {
  color: #fff !important;
}

.main-content {
  overflow: hidden;
}
.main-content .btn {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  line-height: 30px;
  padding-left: 30px;
  padding-right: 30px;
  position: relative;
}
.main-content .btn:before {
  content: "";
  background-color: rgba(255, 255, 255, 0.1);
  position: absolute;
  width: 0;
  height: 0;
  left: 50%;
  top: 50%;
  opacity: 0;
  transition: all 0.3s;
  z-index: 1;
}
.main-content .btn:hover:before, .main-content .btn.active:before {
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  left: -1px;
  top: -1px;
  opacity: 1;
}

.accordion-flush .accordion-item .accordion-button {
  font-family: "Poppins", sans-serif;
  color: #101112;
  font-weight: 600;
  font-size: 17px;
  line-height: 24px;
  padding: 19px 30px 19px 16px;
}
.accordion-flush .accordion-item .accordion-button .icon {
  position: absolute;
  right: 16px;
  top: 16px;
  width: 24px;
  height: 24px;
  transform: rotate(45deg);
  transition: all 0.25s ease-in-out;
}
.accordion-flush .accordion-item .accordion-button.collapsed .icon {
  transform: rotate(0deg);
}
.accordion-flush .accordion-item .accordion-button::after {
  display: none;
}
.accordion-flush .accordion-item .accordion-body {
  color: #747474;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  padding: 4px 16px 16px;
}

.swiper-slide > img {
  display: block;
  width: 100%;
}

.auto-width-slider .swiper-slide {
  width: auto;
}
.auto-width-slider .swiper-slide > img {
  width: 200px;
}

.vertical-slider {
  height: 200px;
}

.slider-with-thumbnail .thumb-slider-outer {
  max-width: 384px;
  position: relative;
  padding-right: 136px;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider {
  position: static;
  margin: 0 0 55px;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-slide {
  border: solid 2px #E4E5EA;
  border-radius: 8px;
  overflow: hidden;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-slide img {
  width: 52px;
  height: 52px;
  object-fit: contain;
  display: block;
  border-radius: 8px;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-slide-thumb-active {
  border-color: #3793D1;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer {
  position: absolute;
  right: 0;
  top: 9px;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-prev,
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-next {
  width: 40px;
  height: 40px;
  border-radius: 40px;
  position: relative;
  left: 0;
  top: 0;
  margin: 0;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-prev svg path,
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-next svg path {
  fill: #101112 !important;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-prev::after,
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-next::after {
  display: none;
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-prev:hover,
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-next:hover {
  background: linear-gradient(91.02deg, #0b0c0e 0%, #072448 100%);
}
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-prev:hover svg path,
.slider-with-thumbnail .thumb-slider-outer .thumb-slider .swiper-button-outer .swiper-button-next:hover svg path {
  fill: #fff !important;
}

